<template>
    <div id="successful-verification">
        <div class="success-checkmark">
            <div class="check-icon">
            <span class="icon-line line-tip"></span>
            <span class="icon-line line-long"></span>
            <div class="icon-circle"></div>
            <div class="icon-fix"></div>
            </div>
            <div class="lower-side">
                <p>
                    Chúc mừng bạn đã xác minh Email thành công.
                </p>
            </div>
        </div>
        
    </div>
</template>

<script>
import axios from 'axios';
export default {

    data() {
        return {
            isLoading: true,
            verifyCode: ''
        };
    },

    mounted() {
        this.getCodeParams()
    },
    methods: {
        getCodeParams() {
            console.log(this.$route)
            if (this.$route.query && this.$route.query.code) {
                this.verifyCode = this.$route.query.code
                this.processVerify()
            }
        },
        processVerify() {
            console.log('cos')
            try {
                axios.post('http://localhost:1337/api/student/verification', {
                    code: this.verifyCode
                }).then(res => {
                    console.log(res)
                }).finally(() => {
                    this.isLoading = false
                })
            } catch (e) {

            }
        }
    },
};
</script>

<style lang="scss" scoped>
#successful-verification {
    min-height: 100vh;
    background-color: beige;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    .success-checkmark {
    width: 400px;
    margin: 0 auto;
    border-radius: 10px;
    box-shadow: 0px 0px 6px #a6a4a4;
    background-color: #fff;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;

        .check-icon {
            width: 80px;
            height: 80px;
            margin: 20px;
            position: relative;
            border-radius: 50%;
            box-sizing: content-box;
            border: 4px solid #4CAF50;

            &::before {
            top: 3px;
            left: -2px;
            width: 30px;
            transform-origin: 100% 50%;
            border-radius: 100px 0 0 100px;
            }

            &::after {
            top: 0;
            left: 30px;
            width: 60px;
            transform-origin: 0 50%;
            border-radius: 0 100px 100px 0;
            animation: rotate-circle 4.25s ease-in;
            }

            &::before,
            &::after {
            content: '';
            height: 100px;
            position: absolute;
            background: #FFFFFF;
            transform: rotate(-45deg);
            z-index: 2;
            }

            .icon-line {
            height: 5px;
            background-color: #4CAF50;
            display: block;
            border-radius: 2px;
            position: absolute;
            z-index: 10;

            &.line-tip {
                top: 46px;
                left: 14px;
                width: 25px;
                transform: rotate(45deg);
                animation: icon-line-tip 0.75s;
            }

            &.line-long {
                top: 38px;
                right: 8px;
                width: 47px;
                transform: rotate(-45deg);
                animation: icon-line-long 0.75s;
            }
            }

            .icon-circle {
            top: -4px;
            left: -4px;
            z-index: 10;
            width: 80px;
            height: 80px;
            border-radius: 50%;
            position: absolute;
            box-sizing: content-box;
            border: 4px solid rgba(76, 175, 80, .5);
            }

            .icon-fix {
            top: 8px;
            width: 5px;
            left: 26px;
            z-index: 1;
            height: 85px;
            position: absolute;
            transform: rotate(-45deg);
            background-color: #FFFFFF;
            }
        }
        .lower-side {
            width: 100%;
            height: 120px;
            margin-top: 20px;
            background-color: #4CAF50;
            overflow: hidden;
            color: #fff;
            border-bottom-right-radius: 10px;
            border-bottom-left-radius: 10px;
            text-align: center;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            font-size: 18px;
        }
        }

        @keyframes rotate-circle {
        0% {
            transform: rotate(-45deg);
        }

        5% {
            transform: rotate(-45deg);
        }

        12% {
            transform: rotate(-405deg);
        }

        100% {
            transform: rotate(-405deg);
        }
        }

        @keyframes icon-line-tip {
        0% {
            width: 0;
            left: 1px;
            top: 19px;
        }

        54% {
            width: 0;
            left: 1px;
            top: 19px;
        }

        70% {
            width: 50px;
            left: -8px;
            top: 37px;
        }

        84% {
            width: 17px;
            left: 21px;
            top: 48px;
        }

        100% {
            width: 25px;
            left: 14px;
            top: 45px;
        }
        }

        @keyframes icon-line-long {
        0% {
            width: 0;
            right: 46px;
            top: 54px;
        }

        65% {
            width: 0;
            right: 46px;
            top: 54px;
        }

        84% {
            width: 55px;
            right: 0px;
            top: 35px;
        }

        100% {
            width: 47px;
            right: 8px;
            top: 38px;
        }
    }
}
</style>